import axios from "axios";
import { popularGamesURL, newGamesURL, upcomingURL, searchGameURL } from "../api";

//------------  Action creator  ----------------

export const loadGames = () => async (dispatch) => {
  // fetch AXIOS
  const popularData = await axios.get(popularGamesURL());
  const upcomingData = await axios.get(upcomingURL());
  const newGameData = await axios.get(newGamesURL());

  dispatch({
    type: "FETCH_GAMES",
    payload: {
      popular: popularData.data.results,
      upcoming: upcomingData.data.results,
      newGame: newGameData.data.results,
    },
  });
};
 export const fetchSearch=(game_name)=>async(dispatch)=>{
   const searchGame=await axios.get(searchGameURL(game_name))
   dispatch({
     type:"FETCH_SEARCH",
     payload:{
        searched:searchGame.data.results,
     }
   })
 }
 export const clearSearch=()=>{
  return{
    type:""
  }
}