const initialState = { game: { platforms: [] }, screen: [], isLoading: false };

function detailReducer(state = initialState, action) {
  switch (action.type) {
    case "GET_DEATAIL":
      return {
        ...state,
        game: action.payload.game,
        screen: action.payload.screen,
        isLoading: true,
      };

    case "UNLOAD_DATA":
      return {
        ...state,
        isLoading: false,
      };
    default:
      return { ...state };
  }
}

export default detailReducer;
