import { useLocation } from "react-router-dom";
import { smallImage } from "../util";
//--------- redux ----------

import { useDispatch } from "react-redux";
import { loadDatail } from "../actions/deatailAction";

//---------- Styling and animation -------------//

import styled from "styled-components";
import { motion } from "framer-motion";
import { Link } from "react-router-dom";

function Game({ name, released, id, image }) {
  const dispatch = useDispatch();
  const path = useLocation().pathname.split("/")[1];
  const url = () => {
    if (path == "") {
      return `${id}`;
    } else {
      return "";
    }
  };
  const loadDetailHandler = () => {
    document.body.style.overflow = "hidden";
    dispatch(loadDatail(id));
  };
  const animId = id.toString();
  //---- load deatails ----
 
  return (
    <StyledGame onClick={loadDetailHandler}>
      <Link to={url()} layoutId={animId}>
        <motion.h3 layoutId={`title${animId}`}>{name}</motion.h3>
        <p>{released}</p>

        <motion.img
          layoutId={`image${animId}`}
          loading="lazy"
          src={smallImage(image, 640)}
          alt={name}
        ></motion.img>
      </Link>
    </StyledGame>
  );
}
const StyledGame = styled(motion.div)`
  box-shadow: 0px 5px 30px rgba(0, 0, 0, 0.2);
  height: 40 vh;
  text-align: center;
  overflow: hidden;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
`;
export default Game;
