const inintState = {
  popular: [],
  newGame: [],
  upcoming: [],
  searched:[],
};

const gameReducer = (state = inintState, aciton) => {
  switch (aciton.type) {
    case "FETCH_GAMES":
      return {
        ...state,
        popular: aciton.payload.popular,
        newGame: aciton.payload.newGame,
        upcoming: aciton.payload.upcoming,
      };
      case "FETCH_SEARCH":
        return{
          ...state,
          searched:aciton.payload.searched,
        }
        case "CLEAR_SEARCH":
          return {
              ...state,
             searched:[],
             }
    default:
      return { ...state };
  }
};

//---------------------------------------

export default gameReducer;
