import { useEffect } from "react";

import { useLocation } from "react-router-dom";
//------------- redux -----------------//

import { useDispatch, useSelector } from "react-redux";
import { loadGames } from "../actions/gamesAction";
import { loadDatail, lodedData } from "../actions/deatailAction";

//--------- components ----------------//

import Game from "../componets/Game";
import GameDeatail from "../componets/GameDeatail";

//-------- styling and animation -------------//

import styled from "styled-components";
import { motion, AnimatePresence, AnimateSharedLayout } from "framer-motion";

function Home() {
  const path = useLocation().pathname.split("/")[1];
  
  const dispatch = useDispatch();

  // useEffect(() => {
  //   if (path !== "") dispatch(loadDatail(path));
  // }, [path]);
  useEffect(() => {
    dispatch(loadGames());
  }, [dispatch]);

  const { popular, newGame, upcoming,searched } = useSelector((state) => state.games);
  const { isLoading, game } = useSelector((state) => state.detail);
  const animId = path.toString();
  return (
    <GameList>
      <AnimateSharedLayout type="crossfade">
        <AnimatePresence>
          {path && <GameDeatail pathId={animId} />}
        </AnimatePresence>
        {searched.length!==0 ? (<> <h2>Search Results</h2>
        <Games>
           {
            searched.map((game) => (
              <Game
                name={game.name}
                released={game.released}
                id={game.id}
                image={game.background_image}
                key={game.id}
              />
            ))}
          ) 
        </Games></>):''}
       
      {upcoming.length && searched.length===0 !== 0 ?(<><h2>Upcoming Games</h2>
        <Games>
           { 
            upcoming.map((game) => (
              <Game
                name={game.name}
                released={game.released}
                id={game.id}
                image={game.background_image}
                key={game.id}
              />
            ))
          }
        </Games></>):""}
       {popular.length !== 0 &&searched.length===0 ? (<><h2>Popular Games</h2>
        <Games>
          { 
            popular.map((game) => (
              <Game
                name={game.name}
                released={game.released}
                id={game.id}
                image={game.background_image}
                key={game.id}
              />
            ))
          }
        </Games></>):''}
        {newGame.length !== 0 &&searched.length===0 ? (<><h2>New Games</h2>
        <Games>
          {
            newGame.map((game) => (
              <Game
                name={game.name}
                released={game.released}
                id={game.id}
                image={game.background_image}
                key={game.id}
              />
            ))
}
        </Games></>):searched.length===0 ?(<h1>Page is Loding .... </h1>):''}
        
      </AnimateSharedLayout>
    </GameList>
  );
}

const GameList = styled(motion.div)`
  h2 {
    padding: 3rem 0rem;
    text-align: center;
  }
`;
const Games = styled(motion.div)`
  margin: auto;

  max-width: 95vw;
  display: grid;
  min-height: 80vh;
  gap: 1.5rem;
  grid-template-columns: repeat(auto-fit, minmax(22rem, 1fr));
`;

export default Home;
