import { useNavigate } from "react-router-dom";
import { smallImage } from "../util";
//------- styling  and Animation --------

import styled from "styled-components";
import { motion } from "framer-motion";

//---------- redux --------
import { useSelector, useDispatch } from "react-redux";
import { unloadData } from "../actions/deatailAction";

//------- image -------
import playstation from "../img/playstation.svg";
import steam from "../img/steam.svg";
import xbox from "../img/xbox.svg";
import nintendo from "../img/nintendo.svg";
import apple from "../img/apple.svg";
import gamepad from "../img/apple.svg";

//----- star image ------
import starEmpty from "../img/star-empty.png";
import starFull from "../img/star-full.png";

//--- get star function ---
const getStars = (game) => {
  const stars = [];
  const rating = Math.floor(game.rating);
  for (let i = 1; i <= 5; i++) {
    if (i <= rating) {
      stars.push(<img alt="stars" key={i} src={starFull} />);
    } else {
      stars.push(<img alt="stars" key={i} src={starEmpty} />);
    }
  }
  return stars;
};

const GameDeatail = ({ pathId }) => {
  const { game, screen, isLoading } = useSelector((state) => state.detail);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const exitDetailHandler = (e) => {
    if (e.target.classList.contains("shadow")) {
      document.body.style.overflow = "auto";
      setTimeout(() => {
        dispatch(unloadData());
      }, 1500);
      navigate("/");
    }
  };

  const animId = pathId.toString();
  return (
    <>
      {isLoading && (
        <CardShadow className="shadow" onClick={exitDetailHandler}>
          <Detail layoutId={animId}>
            <Stats>
              <div className="rating">
                <motion.h3 layoutId={`title${animId}`}>{game.name}</motion.h3>
                <p>rating{game.rating}</p>
                <div className="stars">{getStars(game)}</div>
              </div>
              <Info>
                <h3>Platforms</h3>
                <Platforms>
                  {game.platforms.map((data) => (
                    <h3 key={data.platform.id}>{data.platform.name}</h3>
                  ))}
                </Platforms>
              </Info>
            </Stats>
            <Media>
              <motion.img
                layoutId={`image${animId}`}
                loading="lazy"
                src={smallImage(game.background_image, 1280)}
                alt={game.name}
              ></motion.img>
            </Media>
            <Description>{game.description_raw}</Description>
            <div className="gallery">
              {screen.map((data) => (
                <img
                  loading="lazy"
                  src={smallImage(data.image, 1280)}
                  key={data.id}
                  alt=""
                />
              ))}
            </div>
          </Detail>
        </CardShadow>
      )}
    </>
  );
};
const CardShadow = styled(motion.div)`
  width: 100%;
  min-height: 100vh;
  overflow-y: scroll;
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 11;
  &::-webkit-scrollbar {
    width: 0.5rem;
  }
  &::-webkit-scrollbar-thumb {
    background-color: #ff7676;
  }
  & ::-webkit-scrollbar-track {
    background-color: white;
  }
`;
const Detail = styled(motion.div)`
  width: 80%;

  border-radius: 1rem;
  padding: 2rem 5rem;
  background: white;
  position: absolute;
  left: 10%;
  color: black;
  .stars {
    display: inline;
    img {
      width: 5%;
    }
  }
  img {
    width: 100%;
  }
`;
const Stats = styled(motion.div)`
  display: flex;
  align-items: center;
  justify-content: space-between;
  h3 {
    padding: 1.5rem 0;
  }
`;

const Info = styled(motion.div)`
  text-align: center;
`;
const Platforms = styled(motion.div)`
  display: flex;
  justify-content: space-evenly;
  img {
    margin-left: 3rem;
  }
`;
const Media = styled(motion.div)`
  margin-top: 5rem;

  img {
    width: 100%;
  }
`;
const Description = styled(motion.div)`
  margin: 5rem 0rem;
`;
export default GameDeatail;
